.Menu {
    display: flex;
    flex-basis: 250px;
    background-color: #075c8d;
}

.Menu nav {
    flex: 1;
}

.Menu ul {
    list-style: none;
    padding: 0;
}

.Menu li {
    display: flex;
}

.Menu li a {
    flex: 1;
    text-decoration: none;
    color: white;
    font-size: 1.5rem;
    margin-bottom: 10px;
    padding: 10px;
}

.Menu li a:hover{
    background-color: #00000020;
}